@tailwind base;
@tailwind components;
.top-bar {
  display: flex;
  justify-content: space-between;
  align-items: center;
  height: 30px;
  background-color: #41544a;
  color: #fff;
  font-size: 12px;
}
.top-bar__contact {
  display: flex;
  align-items: center;
  column-gap: 30px;
}
.top-bar__info {
  display: flex;
  align-items: center;
}
.top-bar__info-icon {
  margin-right: 8px;
}
.top-bar__dropdown-group {
  display: flex;
  align-items: center;
  column-gap: 38px;
}
.top-bar__dropdown {
  display: flex;
}
.top-bar__dropdown-name {
  opacity: 70%;
  margin-right: 12px;
}
.top-bar__dropdown-arrow {
  color: #fff;
  margin-left: 10px;
}

.logo {
  display: flex;
  align-items: center;
}
.logo__img {
  margin-right: 15px;
  width: 50px;
  height: 60px;
}
.logo__heading {
  font-size: 28px;
  line-height: 34.5px;
  font-weight: bolder;
}
.logo__subheading {
  color: #86BC42;
  font-size: 12px;
  text-align: end;
}

.logo-2 {
  display: flex;
  align-items: center;
  font-weight: 500;
}
.logo-2__img {
  margin-right: 15px;
  width: 50px;
  height: 60px;
}
.logo-2__heading {
  font-size: 28px;
  line-height: 34.5px;
  color: #fff;
}
.logo-2__subheading {
  color: #86bc42;
  font-size: 12px;
  text-align: end;
}

.search-bar {
  max-width: 750px;
  width: 100%;
  border: 1px solid #ebebeb;
  border-radius: 4px;
  display: flex;
}
.search-bar__drop-down {
  padding: 14px 22px;
  position: relative;
  font-size: 14px;
}
.search-bar__drop-down::after {
  content: "";
  width: 1px;
  height: 22px;
  background-color: #ebebeb;
  display: inline-block;
  position: absolute;
  right: 0px;
  top: 50%;
  transform: translateY(-50%);
}
.search-bar__drop-down-text {
  font-weight: bold;
  margin-right: 28px;
}
.search-bar__input {
  flex-grow: 1;
  padding: 14px 22px;
  font-size: 14px;
}
.search-bar__input::placeholder {
  color: #4f4f4f;
}
.search-bar__submit {
  padding: 14px 22px;
  background-color: #86bc42;
  color: #fff;
  border-radius: 0 4px 4px 0;
  font-size: 20px;
  margin: -1px -1px -1px 0;
}

.search-bar-2 {
  position: relative;
}
.search-bar-2__input {
  background-color: #fff;
  color: #4f4f4f;
  font-size: 14px;
  font-weight: 400;
  width: 100%;
  height: 60px;
  border: 2px solid #ebebeb;
  border-radius: 4px;
  padding-left: 20px;
  padding-right: 90px;
}
.search-bar-2__btn {
  font-size: 25px;
  border-radius: 4px;
  padding: 0 28px;
  position: absolute;
  right: 0;
  top: 0;
  bottom: 0;
}

.store-location {
  border-bottom: 1px solid #ebebeb;
  padding-bottom: 40px;
  margin-bottom: 30px;
}
.store-location__heading {
  font-size: 18px;
  font-weight: 600;
  color: #121212;
  text-transform: uppercase;
  margin-bottom: 13px;
}
.store-location__info {
  font-size: 14px;
  font-weight: 400;
  color: #4f4f4f;
  line-height: 24px;
  margin-bottom: 21px;
}
.store-location__btn {
  display: inline-block;
  font-size: 13px;
  font-weight: 700;
  color: #86bc42;
}
.store-location__underline {
  display: block;
  width: 68px;
  height: 2px;
  background-color: #86bc42;
  position: relative;
  top: 8px;
}

.categories-button {
  border-radius: 4px;
  background-color: #86bc42;
  padding: 16px 21px;
  color: #fff;
  display: flex;
  align-items: center;
}
.categories-button__nav-icon {
  font-size: 25px;
}
.categories-button__text {
  font-size: 12px;
  font-weight: bold;
  margin-left: 22px;
  margin-right: 36px;
}
.categories-button__arrow {
  font-size: 12px;
}

.account-info {
  display: flex;
  column-gap: 30px;
  font-size: 20px;
}
.account-info__icon {
  display: flex;
  align-items: center;
  position: relative;
}
.account-info__badge {
  width: 16px;
  height: 16px;
  border-radius: 50%;
  background-color: #86bc42;
  color: #fff;
  font-size: 10px;
  display: flex;
  justify-content: center;
  align-items: center;
  position: absolute;
  top: -6px;
  left: -9px;
}
.account-info__cart-total-price {
  font-size: 13px;
  font-weight: 700;
  color: #121212;
  margin-left: 10px;
}

.account-info-mobile {
  display: flex;
  justify-content: space-around;
  padding: 9px 0;
  border-radius: 70px;
  background-color: #fff;
}
.account-info-mobile__group {
  display: flex;
  flex-direction: column;
  align-items: center;
  row-gap: 5px;
}
.account-info-mobile__title {
  color: #121212;
  font-size: 13px;
  font-weight: 700;
  text-transform: uppercase;
}

.address {
  background-color: #41544a;
  color: #fff;
  font-size: 14px;
  font-weight: 400;
}
.address__detail {
  line-height: 30px;
  margin-top: 22px;
  margin-bottom: 32px;
}
.address__btn {
  display: inline-block;
  margin-bottom: 40px;
}
.address__btn-effect {
  display: block;
  width: 48px;
  height: 2px;
  background-color: #fff;
  position: relative;
  top: 7px;
}
.address__social {
  background-color: #41544a;
  display: flex;
  column-gap: 26px;
  align-items: center;
}

.nav-bar {
  display: flex;
  column-gap: 20px;
  align-items: flex-end;
}
.nav-bar__menu {
  display: flex;
  column-gap: 10px;
  align-items: center;
  font-weight: 700;
  font-size: 13px;
  color: #121212;
  padding-top: 25px;
  border-top: 2px solid transparent;
  transition: all 0.25s ease-in;
}
.nav-bar__menu:hover {
  border-color: #86bc42;
  color: #86bc42;
}
.nav-bar__arrow-icon {
  font-size: 8px;
}
.nav-bar__menu--active {
  color: #86bc42;
  border-color: #86bc42;
}

.hotline {
  color: #121212;
  display: flex;
  align-items: center;
  font-size: 13px;
  font-weight: 700;
}
.hotline__text {
  margin-left: 10px;
  margin-right: 2px;
}
.hotline__number {
  color: #86bc42;
}

.card-category {
  text-transform: uppercase;
  display: inline-flex;
  flex-direction: column;
  row-gap: 20px;
  align-items: center;
}
.card-category:hover .card-category__thumbnail {
  border-color: #86bc42;
}
.card-category:hover .card-category__name {
  color: #86bc42;
}
.card-category__thumbnail {
  transition: all 0.25s ease-in;
  width: 125px;
  height: 125px;
  border: 1px solid #ebebeb;
  border-radius: 50%;
  display: flex;
  align-items: center;
  justify-content: center;
}
.card-category__thumbnail--active {
  border-color: #86bc42;
}
.card-category__name {
  transition: all 0.25s ease-in;
  font-size: 14px;
  font-weight: 700;
}
.card-category__name--active {
  color: #86bc42;
}

.hero {
  aspect-ratio: 192/60;
  position: relative;
  background-color: #f2e2c4;
}
.hero__intro {
  color: #41544a;
  position: relative;
  z-index: 1;
}
.hero__text-1 {
  font-size: 14px;
  font-weight: 700;
  letter-spacing: 0.1em;
}
.hero__text-2 {
  font-size: 60px;
  font-weight: 700;
  margin: 10px 0;
}
.hero__text-2-separate {
  color: #86bc42;
  font-weight: 400;
}
.hero__text-3 {
  font-size: 16px;
  font-weight: 400;
  margin-bottom: 30px;
}
.hero__text-3-separate {
  font-weight: 700;
}
.hero__btn {
  font-size: 13px;
  font-weight: 700;
  color: #fff;
  padding: 17px 40px;
  background-color: #41544a;
  border-radius: 4px;
}
.hero__farmer {
  width: 50%;
  height: 100%;
  object-fit: cover;
  position: absolute;
  top: 0;
  right: 0;
}

.hero-2 {
  display: flex;
  flex-direction: column;
  align-items: flex-start;
  aspect-ratio: 192/65;
  padding-bottom: 125px;
  position: relative;
}
.hero-2::after {
  content: "";
  position: absolute;
  background-color: rgba(255, 255, 255, 0.6);
  inset: 0;
}
.hero-2__bg {
  position: absolute;
  inset: 0;
  width: 100%;
  height: 100%;
  object-fit: cover;
}
.hero-2__content {
  position: relative;
  z-index: 1;
  margin-top: 68px;
}
.hero-2__text-1 {
  font-size: 14px;
  font-weight: 700;
  color: #849d6a;
  letter-spacing: 0.1em;
  text-transform: uppercase;
  margin-top: 36.5px;
}
.hero-2__text-2 {
  font-size: 60px;
  font-weight: 600;
  color: #41544A;
  text-transform: uppercase;
  margin: 20px 0;
}
.hero-2__text-3 {
  font-size: 14px;
  font-weight: 400;
  color: #4F4F4F;
  margin-bottom: 26px;
}
.hero-2__btn {
  font-size: 13px;
  font-weight: 700;
  color: #fff;
  background-color: #86BC42;
  padding: 17px 47px;
  border-radius: 4px;
}

.hero-3 {
  display: flex;
  flex-direction: column;
  aspect-ratio: 192/30;
  position: relative;
  background-color: #f2e2c4;
}
.hero-3__intro {
  color: #4f4f4f;
  position: relative;
  z-index: 1;
}
.hero-3__text-2 {
  font-size: 50px;
  font-weight: 700;
  line-height: 61.6px;
  text-transform: uppercase;
  margin-bottom: 5px;
}
.hero-3__btn {
  font-size: 14px;
  font-weight: 400;
  text-transform: capitalize;
  transition: text-decoration 0.25s ease-in;
}
.hero-3__btn--bold {
  font-weight: bolder;
}
.hero-3__btn:hover {
  text-decoration: underline;
}
.hero-3__farmer {
  width: 50%;
  height: 100%;
  object-fit: contain;
  position: absolute;
  right: 0;
}

.hero-4 {
  background-color: #f2e2c4;
  padding: 48px 0 28px 0;
}
.hero-4__btn {
  color: #4f4f4f;
  font-size: 14px;
  font-weight: 400;
  text-transform: capitalize;
  transition: text-decoration 0.25s ease-in;
}
.hero-4__btn--bold {
  font-weight: bolder;
}
.hero-4__btn:hover {
  text-decoration: underline;
}

.heading-section {
  font-size: 30px;
  font-weight: 600;
  text-transform: uppercase;
  text-align: center;
  display: flex;
  gap: 10px;
  justify-content: center;
  align-items: center;
}
.heading-section__badge {
  color: #fff;
  font-size: 12px;
  padding: 8px 30px;
  background-color: #86bc42;
  border-radius: 30px;
}

.ft-product-badge {
  display: flex;
  color: #4f4f4f;
  font-size: 12px;
  font-weight: 700;
  letter-spacing: 0.1em;
}
.ft-product-badge__btn {
  transition: all 0.25s ease-out;
  text-transform: uppercase;
  padding: 8px 18px;
  border-radius: 30px;
}
.ft-product-badge__btn:hover {
  color: #fff;
  background-color: #41544a;
}
.ft-product-badge__btn--active {
  text-transform: uppercase;
  padding: 8px 18px;
  border-radius: 30px;
  color: #fff;
  background-color: #41544a;
}

.form {
  padding: 90px;
  background-color: #f5f5f5;
}
.form__heading {
  font-size: 24px;
  font-weight: 600;
  color: #121212;
  text-transform: uppercase;
  margin-bottom: 30px;
}
.form__input-heading {
  display: block;
  font-size: 14px;
  font-weight: 400;
  color: #121212;
  margin-bottom: 6px;
}
.form__input {
  width: 100%;
  height: 55px;
  border: 1px solid #ebebeb;
  border-radius: 4px;
  padding: 0 15px;
  margin-bottom: 30px;
}
.form__input--large {
  width: 100%;
  height: 150px;
}
.form__btn {
  font-size: 13px;
  font-weight: 700;
  color: #fff;
  padding: 17px 45px;
  background-color: #86bc42;
  border-radius: 4px;
}

.form-login {
  padding: 60px;
  border: 1px solid #ebebeb;
  border-radius: 4px;
}
.form-login__title {
  display: inline-block;
  font-size: 18px;
  font-weight: 600;
  color: #121212;
  opacity: 0.3;
  margin-right: 57px;
  margin-bottom: 30px;
  transition: all 0.25s ease-in;
}
.form-login__title--active {
  opacity: 1;
  text-decoration: underline;
}
.form-login__title:hover {
  opacity: 1;
  text-decoration: underline;
}
.form-login__section {
  margin-bottom: 30px;
}
.form-login__section--margin {
  margin-bottom: 18px;
}
.form-login__section--flex {
  display: flex;
  align-items: center;
}
.form-login__label {
  font-size: 14px;
  font-weight: 400;
  color: #121212;
}
.form-login__label--underline {
  text-decoration: underline;
  margin-left: auto;
}
.form-login__input-text {
  width: 100%;
  height: 55px;
  border: 1px solid #ebebeb;
  border-radius: 4px;
  padding: 0 15px;
  margin-top: 6px;
}
.form-login__input-checkbox {
  width: 16px;
  height: 16px;
  border-radius: 4px;
  margin-right: 8px;
}
.form-login__btn {
  font-size: 13px;
  font-weight: 700;
  color: #fff;
  border-radius: 4px;
  background-color: #86bc42;
  padding: 17px 56px;
}

.form-register {
  padding: 60px;
