}
.form-register__label {
  font-size: 14px;
  font-weight: 400;
  color: #121212;
}
.form-register__input {
  width: 100%;
  height: 55px;
  border: 1px solid #ebebeb;
  border-radius: 4px;
  padding: 0 15px;
  margin-top: 6px;
}
.form-register__note {
  font-size: 14px;
  font-weight: 400;
  color: #4f4f4f;
  margin-bottom: 30px;
}
.form-register__btn {
  font-size: 13px;
  font-weight: 700;
  color: #fff;
  border-radius: 4px;
  background-color: #86bc42;
  padding: 17px 55px;
}

.form-order-tracking {
  padding: 60px;
