}
.form-order-tracking__title {
  display: inline-block;
  font-size: 18px;
  font-weight: 600;
  color: #121212;
  opacity: 0.3;
  margin-right: 57px;
  margin-bottom: 30px;
  transition: all 0.25s ease-in;
}
.form-order-tracking__title--active {
  opacity: 1;
  text-decoration: underline;
}
.form-order-tracking__title:hover {
  opacity: 1;
  text-decoration: underline;
}
.form-order-tracking__section {
  margin-bottom: 30px;
}
.form-order-tracking__section--margin {
  margin-bottom: 20px;
}
.form-order-tracking__label {
  font-size: 14px;
  font-weight: 400;
  color: #121212;
}
.form-order-tracking__input {
  width: 100%;
  height: 55px;
  border: 1px solid #ebebeb;
  border-radius: 4px;
  padding: 0 15px;
  margin-top: 6px;
}
.form-order-tracking__note {
  font-size: 14px;
  font-weight: 400;
  color: #4f4f4f;
  margin-bottom: 30px;
}
.form-order-tracking__btn {
  font-size: 13px;
  font-weight: 700;
  color: #fff;
  border-radius: 4px;
  background-color: #86bc42;
  padding: 17px 55px;
}

.faq-menu {
  border: 1px solid #ebebeb;
  border-radius: 4px;
  padding: 30px 0px 37px 30px;
  color: #121212;
}
.faq-menu__heading {
  font-size: 18px;
  font-weight: 600;
  margin-bottom: 30px;
}
.faq-menu__categories {
  display: flex;
  flex-direction: column;
  row-gap: 20px;
}
.faq-menu__category {
  transition: color 0.25s ease-in;
}
.faq-menu__category:hover {
  color: #86bc42;
}
.faq-menu__category--active {
  color: #86bc42;
}

.faq-answer {
  color: #121212;
}
.faq-answer__heading {
  text-transform: uppercase;
  font-size: 24px;
  font-weight: 600;
  margin-bottom: 25px;
}
.faq-answer__container {
  margin-bottom: 19px;
}
.faq-answer__container:hover .faq-answer__sign-1 {
  display: inline;
}
.faq-answer__container:hover .faq-answer__sign-2 {
  display: none;
}
.faq-answer__container:hover .faq-answer__text {
  display: block;
}
.faq-answer__question {
  display: flex;
  justify-content: space-between;
  font-size: 16px;
  font-weight: 700;
  border-bottom: 1px solid #121212;
  padding-bottom: 15px;
}
.faq-answer__sign-1 {
  display: none;
}
.faq-answer__text {
  color: #4f4f4f;
  padding-right: 25px;
  margin-top: 30px;
  display: none;
}
.faq-answer__text--active {
  display: block;
}

.ft-product-card {
  display: flex;
  flex-direction: column;
  align-items: center;
  row-gap: 10px;
  color: #121212;
  border: 1px solid transparent;
  border-radius: 4px;
  padding: 11px 30px 30px 30px;
  transition: border-color 0.25s ease-out;
}
.ft-product-card--active {
  border: 1px solid #ebebeb;
  padding: 11px 30px 30px 30px;
}
.ft-product-card:hover {
  border-color: #ebebeb;
}
.ft-product-card:hover .ft-product-card__behaviour {
  opacity: 1;
  visibility: visible;
}
.ft-product-card:hover .ft-product-card__price {
  opacity: 0;
  visibility: hidden;
}
.ft-product-card:hover .ft-product-card__btn {
  opacity: 1;
  visibility: visible;
}
.ft-product-card__thumbnail {
  position: relative;
  aspect-ratio: 1/1;
  width: calc(100% - 40px);
}
.ft-product-card__img {
  object-fit: cover;
  width: 100%;
  height: 100%;
  position: absolute;
  inset: 0;
}
.ft-product-card__title {
  font-size: 12px;
  font-weight: 700;
  color: #4f4f4f;
  letter-spacing: 0.1em;
  text-transform: uppercase;
}
.ft-product-card__star {
  font-size: 10px;
  color: #f2b23e;
}
.ft-product-card__star--grey {
  color: #ebebeb;
}
.ft-product-card__name {
  font-size: 14px;
  font-weight: 400;
  text-transform: capitalize;
  text-align: center;
}
.ft-product-card__price-container {
  position: relative;
  width: 100%;
  height: 45px;
  text-align: center;
}
.ft-product-card__price {
  position: absolute;
  font-size: 16px;
  font-weight: 700;
  color: #86bc42;
  width: 100%;
  text-align: center;
  transition: opacity 0.25s ease-in;
}
.ft-product-card__btn {
  display: flex;
  position: absolute;
  transition: opacity 0.25s ease-in;
  opacity: 0;
  visibility: hidden;
  column-gap: 15px;
  justify-content: center;
  align-items: center;
  width: 100%;
  height: 100%;
  font-size: 13px;
  font-weight: 700;
  color: #fff;
  background-color: #86bc42;
  border-radius: 4px;
}
.ft-product-card__behaviour {
  display: flex;
  transition: opacity 0.25s ease-out;
  opacity: 0;
  visibility: hidden;
  flex-direction: column;
  align-items: center;
  row-gap: 22px;
  position: absolute;
  top: 9px;
  right: -30px;
}
.ft-product-card__save {
  width: 39px;
  height: 39px;
  border: 1px solid #86bc42;
  border-radius: 50%;
  background-color: #fff;
  display: flex;
  justify-content: center;
}
.ft-product-card__btn--active {
  opacity: 1;
  visibility: visible;
}
.ft-product-card__behaviour--active {
  opacity: 1;
  visibility: visible;
}
.ft-product-card__tag {
  display: flex;
  flex-direction: column;
  row-gap: 10px;
  font-size: 12px;
  font-weight: 700;
  color: #fff;
  text-transform: uppercase;
